.validateTokenComponent-button{
    width: 10vw;
    height: 6vh;
    color : red;
    padding: 0.2% 0.2% 0.2% 0.2%;
    font-family:sans-serif;
    font-weight: bold;
    border: solid 0.1vw orangered;
    border-radius: 5%;
    word-wrap: break-word;
}

.validateTokenComponent-button:active{
    font-weight: bold;
}

.validateTokenComponent-text{
    padding: 2%;
    color: purple;
    font-family :  sans-serif;
    font-weight: bold;
}

.validator{
    width: 50%;
    height: 20vh;
    margin:2% 25%;
    padding:2%;
    border : solid 0.1vw yellowgreen;
}

.validator-text-field{
    width : 20vw;
    height : 5vh;
    color: darkgreen;
}

.validator-button-reset{
    width : 10%;
    height: 6vh;
}

.validator-output{
    width: 37vw;
    height: 5vh;
    margin: 5% 2%;
    padding:2%;
    background-color: whitesmoke;
    color: blue;
    font-weight: bold;
    font-family: sans-serif;
    border:solid 0.1vw yellowgreen
}