.generate-token-checkbox{
    display: flex;
    padding: 0.2%;
    margin: 2% 2%;
}

.generate-token-label{
    color: purple;
    font-family: sans-serif;
    padding: 1%;
    font-weight:bold;
}

.generate-token-button{
    margin:2% 30%;
    padding:2%;
    color : darkblue;
    font-family: sans-serif;
    font-weight: bold;
    border: solid 0.1vw green;
    border-radius: 1% 1% 1%;
    width: 10vw;
    height: 6vh;
    word-wrap: break-word;

}

.generate-token-button:active{
    background-color:rgb(171, 218, 234);
}

.generate-token-between-checkbox{
    padding: 0.05%
}

.generate-token{
    width:50%;
    height:50vh;
    padding: 2%;
    margin: 2% 25%;
    border: solid 0.1vw yellowgreen

}

.generate-token-output{
    width: 37vw;
    height: 5vh;
    margin: 5% 2%;
    padding:2%;
    background-color: whitesmoke;
    color: blue;
    font-weight: bold;
    font-family: sans-serif;
    border:solid 0.1vw yellowgreen

}



.generate-token-div{
    padding: 2%;
    margin: 2%;
}

.generate-token-button-div{
    display: flex;
    margin-right: 50%;
}